    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding: 0 16px;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 100;
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s, visibility .25s, all cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  background-color: var(--bg-ft-color);
  border-radius: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (max-width: 479px) {
  .modal {
    width: calc(100% - 40px);
  }
}

@media screen and (min-width: 320px) and (max-width: 1199px) {
  .modal {
    width: 450px;
    height: 609px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
    height: 581px;
  }
}

.btn-close {
  box-sizing: inherit;
  width: 30px;
  height: 30px;
  background-color: var(--bg-ft-color);
  cursor: pointer;
  transition: fill var(--anim-standart);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.btn-close:hover, .btn-close:focus {
  fill: var(--accent-color);
}

.modal__form {
  padding: 40px;
}

.modal__invocation {
  text-align: center;
  letter-spacing: .03em;
  color: var(--main-black-text-color);
  margin-bottom: 12px;
  font-size: 20px;
  line-height: 1.5;
  display: block;
}

@media screen and (max-width: 1199px) {
  .modal__invocation {
    max-width: 370px;
  }
}

@media screen and (min-width: 1200px) {
  .modal__invocation {
    width: 448px;
  }
}

.modal__field {
  margin-bottom: 10px;
  display: block;
  position: relative;
}

.modal__field:focus-within {
  fill: var(--accent-color);
}

.modal__field:focus-within .modal__field-input {
  border-color: var(--accent-color);
}

.modal__label-content {
  letter-spacing: .01em;
  color: var(--main-grey-text-color);
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

.modal__field-input {
  width: 100%;
  height: 40px;
  cursor: pointer;
  transition: border var(--anim-standart);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  padding-left: 42px;
  padding-right: 5px;
  display: block;
}

.modal__input-icon {
  transition: fill var(--anim-standart);
  position: absolute;
  bottom: 35%;
  left: 14px;
  transform: translateY(50%);
}

.modal__input-icon:focus {
  fill: var(--accent-color);
}

.modal__field-textarea {
  margin-bottom: 20px;
  display: block;
}

.modal__field-textarea textarea {
  resize: none;
  width: 100%;
  height: 120px;
  letter-spacing: .01em;
  color: var(--main-grey-text-color);
  cursor: pointer;
  transition: border var(--anim-standart);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  padding: 12px 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

textarea:focus {
  border-color: var(--accent-color);
}

.modal__field-checkbox {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  display: flex;
}

.modal__checkbox-icon {
  width: 16px;
  height: 15px;
  outline: 2px solid var(--main-black-text-color);
  outline-offset: -2px;
  transition: var(--anim-standart);
  border-radius: 2px;
  margin-right: 7px;
}

.modal__input-checkbox:checked + .modal__checkbox-icon {
  background-color: var(--accent-color);
  outline-color: var(--accent-color);
}

.checkbox-icon {
  fill: var(--white-color);
}

.modal__checkbox-text {
  font-weight: 400;
}

@media screen and (max-width: 1199px) {
  .modal__checkbox-text {
    font-size: 12px;
    line-height: 1.16;
  }
}

@media screen and (min-width: 1200px) {
  .modal__checkbox-text {
    font-size: 14px;
    line-height: 1.71;
  }
}

.modal__checkbox-link {
  color: var(--accent-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.71;
  text-decoration: underline;
}

@media screen and (max-width: 1199px) {
  .modal__checkbox-link {
    font-size: 12px;
    line-height: 1.16;
  }
}

.modal__btn-send {
  width: 200px;
  height: 50px;
  background-color: var(--accent-color);
  cursor: pointer;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  display: block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.modal__btn-send span {
  letter-spacing: .06em;
  color: var(--white-color);
  padding: 10px 55px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.87;
